.infoWrapper {
    text-align: center;
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    padding: 1rem;
    max-width: 720px;
}
.jumbo {
    padding: 5rem;
    background-image: url('./../../common/1872816.jpg');
    background-size: cover;
    color: #012998;

  -webkit-text-fill-color: white; /* Will override color (regardless of order) */
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: black;
  text-shadow: 2px 2px 4px black;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.flex1 {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    background-color: #DBE5FF;
    padding: 1rem;
    border-radius: 0.5rem;
    box-shadow: 1px 1px 3px black;
}
.flexInfo {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    gap: 1rem;
}
.info h2 {
    padding: 2rem 0;
}
.flex2 {
    display: flex;
    gap: 1rem;
    justify-content: center;
    margin-top: 1rem;
    flex-wrap: wrap;
}
.char-links {
    text-decoration: none;
    padding: 1rem;
    border: 1px solid #091534;
    border-radius: 1rem;
    background-color: #ffe283;
    width: 10rem;
    color: #091534;
    font-weight: bold;
}
.char-links:hover {

    background-color: #925300;
    color:white;
}
.infoBtn {
    padding: 1rem;
    height: 3rem;
    border: none;
    width: 15rem;
    font-size: 1rem;
    color: #012998;
    box-shadow: 2px 2px 6px black;
}
.infoBtn:hover {
    font-weight: bold;
    background-color: #678FFE
}
