.char {
    width: 250px;
    display: flex;
    flex-direction: column;
    align-items: center;
    align-self:flex-start;
    background-color: #DBE5FF;
    border-radius: 12px;
    overflow: hidden;
    position: relative;
    box-shadow: 2px 2px 5px #091534;
}
.char:hover {
    box-shadow: none;
}
.char:hover > .infoContainer {
    visibility: visible;
    box-shadow: 0px 0px 8px #091534 inset;
}
.char>h3 {
    width: 100%;
    margin: 0;
    color: white;
    padding: 10px 0;
    background-color: #012998;
    text-align: center;
}
.char>img {
    width: 100%;
    display: block;
    background-color: #DBE5FF
}


.infoContainer {
    position: absolute;
    background-color: hsla(0, 0%, 100%, .8);
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    color: #091534;
    visibility: hidden;
    display: flex;
    align-items: center;
    align-content: center;
    justify-content: center;
    flex-direction: column;
    font-weight: bold;
    overflow: auto;
    cursor: pointer;
}

.infoContainer > p {
    width: 80%;
    margin: 0%;
    padding: 0%;
    text-align: center;
}
