.gotopage {
    display: flex;
    gap: 1rem;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    padding: 0 1rem;
}
.gotopage > button {
    background-color: #DBE5FF;
    border: 1px solid #091534;
    width: 2rem;
    height: 2rem;
    border-radius: 1rem;
}
.gotopage > input {
    padding: 0.5rem;
    background-color: #DBE5FF;
    border: 1px solid #091534;
    outline: none;
}